import React from 'react';
import TweenMax from 'gsap/TweenMax';

import { ContactContainer } from './ContactStyle.js';

class Contact extends React.Component {
  contructor() {
    this.whatsappIcon = React.createRef();
  }

  componentDidMount() {
    TweenMax.fromTo(
      this.whatsappIcon,
      1,
      { rotation: 0 },
      {
        rotation: 20,
        delay: 5,
        repeat: -1,
        repeatDelay: 0,
        yoyo: true,
        immediateRender: false
      }
    );
  }

  render() {
    return (
      <ContactContainer>
        <div className="contact-title">
          <h1>Contato</h1>
        </div>
        <div className="contact-description-justified">
          Precisando de um oramento para o seu projeto? Entre em contato com a gente atravs do formulrio abaixo!
          Crticas e sugestes tambm so sempre bem vindas! Caso tenha alguma dvida, ou precise de uma resposta mais
          rpida, tambm estamos disponveis no Whatsapp. Clique no boto abaixo para chamar!
        </div>
        <br />
        <div
          className="whatsapp-badge"
          title="Chamar por Whatsapp"
          ref={(elem) => this.whatsappIcon = elem}
        />
        <div className="form-container">
          <form>
            <input className="form form-control" placeholder="Seu email" />
            <br />
            <input className="form form-control" placeholder="Ttulo da mensagem" />
            <br />
            <textarea className="form form-control" placeholder="Digite sua mensagem" rows="10" />
            <button className="btn btn-primary">
              <span>ENVIAR</span>
              <i class="fa fa-paper-plane" aria-hidden="true" />
              </button>
          </form>
        </div>
      </ContactContainer>
    );
  }
}

export default Contact;
